body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  padding: 32px 19px 40px;
  display: block;
}

.card__name {
  text-align: left;
  color: #060b35;
  padding: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  padding: 0 16px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  padding: 0 16px 16px;
  display: flex;
}

.card__text {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__count {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buybutton {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 0 auto;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  display: block;
}

.card__buybutton:hover {
